import React from "react";
import { Link } from "react-router-dom";

// icons
import { MdOutlineCreate } from "react-icons/md";
import { BsCardChecklist } from "react-icons/bs";
import { FaUsers, FaUserSecret } from "react-icons/fa";
import { HiOutlineQueueList,  } from "react-icons/hi2";
import {HiOutlineDocumentReport} from 'react-icons/hi'

const AdminNavs = () => {
  return (
    <header className="text-gray-600 body-font py-10">
      <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
        <nav className="md:mr-auto md:ml-4 md:py-1 md:pl-4 md:border-l md:border-gray-400	flex flex-wrap items-center text-base justify-center">
          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/create"
          >
            Create Products
            <MdOutlineCreate className="mx-1" color="blue" />
          </Link>
          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/orders"
          >
            Orders
            <BsCardChecklist className="mx-1" color="blue" />
          </Link>
          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/products"
          >
            Products
            <HiOutlineQueueList className="mx-1" color="blue" />
          </Link>
          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/users"
          >
            Users
            <FaUsers className="mx-1" color="blue" />
          </Link>

          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/suppliers"
          >
            Suppliers
            <FaUserSecret className="mx-1" color="blue" />
          </Link>
          <Link
            className="mr-5 hover:text-gray-900 inline-flex items-center"
            to="/admin/reports"
          >
            Reports
            <HiOutlineDocumentReport className="mx-1" color="blue" />
          </Link>
        </nav>
      </div>
    </header>
  );
};

export default AdminNavs;
