import axios from "axios";
import { toast } from "react-toastify";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import AdminNavs from "../../components/Admin/AdminNavs";
import { productListDetail, updateProduct } from "../../actions/productAction";

const ProductEditScreen = ({ match, history }) => {
  const dispatch = useDispatch();
  const productId = match.params.id;

  const [name, setname] = useState("");
  const [price, setprice] = useState(0);
  const [image, setimage] = useState("");
  const [brand, setbrand] = useState("");
  const [category, setcategory] = useState("");
  const [countInStock, setcountInStock] = useState(0);
  const [description, setdescriptions] = useState("");
  const [keyMarket, setkeyMarket] = useState("");

  const [uploading, setuploading] = useState(false);

  const productDetail = useSelector((state) => state.productDetail);
  const { loading, error, product } = productDetail;

  const productUpdate = useSelector((state) => state.productUpdate);
  const {
    loading: loadingUpdate,
    error: errorUpdate,
    success: successUpdate,
  } = productUpdate;

  useEffect(() => {
    if (successUpdate) {
      dispatch({ type: "product_update_reset" });
    } else {
      if (!product.name || product._id !== productId) {
        dispatch(productListDetail(productId));
      } else {
        setname(product.name);
        setprice(product.price);
        setimage(product.image);
        setdescriptions(product.description);
        setbrand(product.brand);
        setcategory(product.category);
        setcountInStock(product.countInStock);
        setkeyMarket(product.keyMarket);
      }
    }
  }, [dispatch, productId, history, product, successUpdate]);

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      updateProduct({
        _id: productId,
        name,
        price,
        image,
        description,
        category,
        countInStock,
        brand,
        keyMarket,
      })
    );
    toast.success("Updated");
  };

  return (
    <>
      <AdminNavs />
      {loadingUpdate && <p> Loading... </p>}
      {errorUpdate && toast.error(`${error}`)}

      <section class="text-gray-600 body-font">
        <div class="container px-5 py-10 mx-auto flex flex-wrap items-center bg-gray-100">
          {/* left box */}
          <div class="lg:w-1/2 md:w-1/2 bg-gray-100 rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <h2 class="text-gray-900 text-lg font-medium title-font mb-5">
              Fill the all feilds*
            </h2>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Product Name
              </label>
              <input
                type="text"
                id=" Product Name"
                name="name"
                required
                value={name}
                onChange={(e) => setname(e.target.value)}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Description
              </label>
              <input
                type="text"
                id=" description"
                name=" description"
                required
                value={description}
                onChange={(e) => setdescriptions(e.target.value)}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Category
              </label>
              <input
                disabled
                type="text"
                name="category"
                value={category}
                onChange={(e) => setcategory(e.target.value)}
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>
          </div>

          {/* right box */}
          <div class="lg:w-1/2 md:w-1/2 bg-gray-100 rounded-lg p-8 flex flex-col md:ml-auto w-full mt-10 md:mt-0">
            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Price
              </label>
              <input
                type="number"
                name="price"
                required
                value={price}
                onChange={e => setprice(e.target.value)}
                id=" description"
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>

            <div class="relative mb-4">
              <label for="full-name" class="leading-7 text-sm text-gray-600">
                Count in stock
              </label>
              <input
                type="number"
                name="countInStock"
                value={countInStock}
                onChange={e => setcountInStock(e.target.value)}
                required
                id=" description"
                class="w-full bg-white rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"
              />
            </div>
            <button
              onClick={submitHandler}
              class="text-white bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg"
            >
              Edit
            </button>
            <p class="text-xs text-gray-500 mt-3">
              Literally you probably haven't heard of them jean shorts.
            </p>
          </div>
        </div>
      </section>
    </>
  );
};

export default ProductEditScreen;
